import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { InputSearchProps } from 'summer/dist/views/components/inputSearch/InputSearch';
import { CLASS_NAME } from '../../../../models/dtos/mfp/customizacaoTermos';
import {
  ABONO_PECUNIARIO,
  ACORDO_PRORROGACAO_HORA_EXTRA,
  AVISO_FERIAS,
  AVISO_PREVIO_ACORDO_INDENIZADO,
  AVISO_PREVIO_ACORDO_TRABALHADO,
  AVISO_PREVIO_INDENIZADO,
  AVISO_PREVIO_TRABALHADO,
  CARTA_PREPOSICAO,
  CARTA_RECOMENDACAO,
  CARTA_SUSPENSAO,
  DECLARACAO_OPCAO_FGTS,
  MSG_ANIVERSARIO,
  MSG_CONTRA_CHEQUE,
  PEDIDO_DEMISSAO,
  PEDIDO_DEMISSAO_ACORDO,
  RECIBO_CARTEIRA_DE_TRABALHO_DEVOLUCAO,
  RECIBO_CARTEIRA_DE_TRABALHO_ENTREGA,
  RECIBO_CESTA_BASICA,
  RECIBO_PRO_LABORE,
  TERMO_ADITIVO,
  TERMO_COMPENSACAO_HORAS,
  VALE_ALIMENTACAO_RECIBO,
  VALE_REFEICAO_RECIBO,
  VALE_TRANSPORTE_RECIBO,
} from '../../../../models/enumerated/mfp/customizacaoTermosTipoEnum';

export interface CustomizacaoTermoSearchViewProps extends InputProps {
  apenasAbonoPecuniario?: boolean;
  apenasTipo?: boolean;
  apenasAcordo?: boolean;
  apenasTermosCompensacaoHoras?: boolean;
  apenasTermoAditivo?: boolean;
  apenasCartaRecomendacao?: boolean;
  apenasAvisoFerias?: boolean;
  apenasReciboEntregaCtps?: boolean;
  apenasReciboDevolucaoCtps?: boolean;
  apenasReciboCestaBasica?: boolean;
  apenasReciboValeTransporte?: boolean;
  apenasDeclaracaoOpcaoFgts?: boolean;
  apenasValeAlimentacaoRefeicaoRecibo?: boolean;
  apenasProrrogacaoHoraExtra?: boolean;
  apenasCartaSuspensao?: boolean;
  apenasCartaPreposicao?: boolean;
  apenasMsgContraCheque?: boolean;
  apenasMsgAniversario?: boolean;
  apenasReciboProLabore?: boolean;
}

const CustomizacaoTermoIS: FC<CustomizacaoTermoSearchViewProps> = props => {
  function getSearchExpression(): SearchExpression[] {
    if (props.apenasAbonoPecuniario) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: ABONO_PECUNIARIO.value,
        },
      ];
    } else if (props.apenasTipo) {
      return [
        {
          alias: 'c1',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: AVISO_PREVIO_TRABALHADO.value,
        },
        {
          alias: 'c2',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: AVISO_PREVIO_INDENIZADO.value,
        },
        {
          alias: 'c3',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: PEDIDO_DEMISSAO.value,
        },
      ];
    } else if (props.apenasAcordo) {
      return [
        {
          alias: 'c1',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: AVISO_PREVIO_ACORDO_TRABALHADO.value,
        },
        {
          alias: 'c2',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: AVISO_PREVIO_ACORDO_INDENIZADO.value,
        },
        {
          alias: 'c3',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: PEDIDO_DEMISSAO_ACORDO.value,
        },
      ];
    } else if (props.apenasTermosCompensacaoHoras) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: TERMO_COMPENSACAO_HORAS.value,
        },
      ];
    } else if (props.apenasTermoAditivo) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: TERMO_ADITIVO.value,
        },
      ];
    } else if (props.apenasCartaRecomendacao) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: CARTA_RECOMENDACAO.value,
        },
      ];
    } else if (props.apenasAvisoFerias) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: AVISO_FERIAS.value,
        },
      ];
    } else if (props.apenasReciboEntregaCtps) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: RECIBO_CARTEIRA_DE_TRABALHO_ENTREGA.value,
        },
      ];
    } else if (props.apenasReciboDevolucaoCtps) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: RECIBO_CARTEIRA_DE_TRABALHO_DEVOLUCAO.value,
        },
      ];
    } else if (props.apenasReciboCestaBasica) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: RECIBO_CESTA_BASICA.value,
        },
      ];
    } else if (props.apenasReciboValeTransporte) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: VALE_TRANSPORTE_RECIBO.value,
        },
      ];
    } else if (props.apenasDeclaracaoOpcaoFgts) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: DECLARACAO_OPCAO_FGTS.value,
        },
      ];
    } else if (props.apenasValeAlimentacaoRefeicaoRecibo) {
      return [
        {
          alias: 'c1',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: VALE_ALIMENTACAO_RECIBO.value,
        },
        {
          alias: 'c2',
          conditionalGroup: 'c_1',
          fieldName: 'tipo',
          value: VALE_REFEICAO_RECIBO.value,
        },
      ];
    } else if (props.apenasProrrogacaoHoraExtra) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: ACORDO_PRORROGACAO_HORA_EXTRA.value,
        },
      ];
    } else if (props.apenasCartaSuspensao) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: CARTA_SUSPENSAO.value,
        },
      ];
    } else if (props.apenasCartaPreposicao) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: CARTA_PREPOSICAO.value,
        },
      ];
    } else if (props.apenasMsgContraCheque) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: MSG_CONTRA_CHEQUE.value,
        },
      ];
    } else if (props.apenasMsgAniversario) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: MSG_ANIVERSARIO.value,
        },
      ];
    } else if (props.apenasReciboProLabore) {
      return [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'tipo',
          value: RECIBO_PRO_LABORE.value,
        },
      ];
    }
    return [];
  }

  const inputSearchProps: InputSearchProps<any> = {
    ...props.inputSearchProps,
    columns: [
      {
        dataField: 'descricao',
        text: 'Descrição',
      },
      {
        dataField: 'termo',
        text: 'Termo',
      },
    ],
    dtoClassName: CLASS_NAME,
    searchExpressions: getSearchExpression,
    searchViewId: 'CustomizacaoTermosSearchView',
  };

  return <InputGroupInline {...props} type={InputType.INPUT_SEARCH} inputSearchProps={inputSearchProps} />;
};

export default CustomizacaoTermoIS;
