import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType, SearchExpression } from 'summer';
import { InputSearchProps } from 'summer/dist/views/components/inputSearch/InputSearch';
import { CLASS_NAME } from '../../../../models/dtos/mfp/vinculoExample';
import { AUTONOMOS_VALUES, EMPREGADO_DOMESTICO, ESTAGIARIO, MENOR_APRENDIZ, SOCIOS_VALUES } from '../../../../models/enumerated/mfp/categoriaEnum';

export interface VinculoInputSearchProps extends InputProps {
  apenasAutonomos?: boolean;
  apenasAprendizes?: boolean;
  apenasDomesticos?: boolean;
  apenasEstagiarios?: boolean;
  apenasInativos?: boolean;
  apenasSocios?: boolean;
}

const VinculoIS: FC<VinculoInputSearchProps> = props => {
  function getSearchExpression(): SearchExpression[] {
    let expressions = [];
    if (props.apenasAutonomos) {
      expressions = [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'categoria',
          value: AUTONOMOS_VALUES,
        },
      ];
    }
    if (props.apenasAprendizes) {
      expressions = [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'categoria',
          value: MENOR_APRENDIZ.value,
        },
      ];
    }
    if (props.apenasDomesticos) {
      expressions = [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'categoria',
          value: EMPREGADO_DOMESTICO.value,
        },
      ];
    }
    if (props.apenasEstagiarios) {
      expressions = [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'categoria',
          value: ESTAGIARIO.value,
        },
      ];
    }
    if (props.apenasSocios) {
      expressions = [
        {
          alias: 'c',
          conditionalGroup: 'c_',
          fieldName: 'categoria',
          value: SOCIOS_VALUES,
        },
      ];
    }
    return expressions;
  }

  const inputSearchProps: InputSearchProps<any> = {
    ...props.inputSearchProps,
    columns: [
      {
        dataField: 'matricula',
        text: 'Matrícula',
      },
      {
        dataField: 'nome',
        text: 'Trabalhador',
      },
      {
        dataField: 'categoria',
        text: 'Categoria',
      },
      {
        dataField: 'dataAdmissao',
        text: 'Dt. Admissão',
      },
      {
        dataField: 'dataAfastamentoMovimentacao',
        text: 'Dt. Último Afastamento',
      },
      {
        dataField: 'dataAfastamentoRecisao',
        text: 'Dt. Rescisão',
      },
      {
        dataField: 'cpf',
        text: 'CPF',
      },
    ],
    dtoClassName: CLASS_NAME,
    searchExpressions: getSearchExpression,
    searchViewId: 'VinculoSearchView',
  };

  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      type={InputType.INPUT_SEARCH}
      inputSearchProps={inputSearchProps}
      searchViewProps={{ apenasInativos: props.apenasInativos }}
    />
  );
};

export default VinculoIS;
